import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

import AntdDemoPage from "@/views/AntdDemoPage.vue";
import CenterTestPage from "@/views/CenterTestPage.vue";
import DebounceThrottlePage from "@/views/DebounceThrottlePage.vue";
import EventModifiers from "@/views/EventModifiers.vue";
import FileUploadPage from "@/views/FileUploadPage.vue";
import HomePage from "@/views/HomePage.vue";
import LoginPage from "@/views/LoginPage.vue";
import PartScrollPage from "@/views/PartScrollPage.vue";
import ScrollPage from "@/views/ScrollPage.vue";
import TodoPage from "@/views/TodoPage.vue";
import TransitionTestPage from "@/views/TransitionTestPage.vue";

const routes = [
  {
    path: "/",
    name: "LoginPage",
    component: LoginPage
  },
  {
    path: "/AntdDemoPage",
    name: "antddemopage",
    component: AntdDemoPage
  },
  {
    path: "/CenterTestPage",
    name: "centertestpage",
    component: CenterTestPage
  },
  {
    path: "/DebounceThrottlePage",
    name: "debouncethrottlepage",
    component: DebounceThrottlePage
  },
  {
    path: "/EventModifiers",
    name: "eventmodifiers",
    component: EventModifiers
  },
  {
    path: "/FileUploadPage",
    name: "fileuploadpage",
    component: FileUploadPage
  },
  {
    path: "/HomePage",
    name: "homepage",
    component: HomePage
  },
  {
    path: "/LoginPage",
    name: "loginpage",
    component: LoginPage
  },
  {
    path: "/PartScrollPage",
    name: "partscrollpage",
    component: PartScrollPage
  },
  {
    path: "/ScrollPage",
    name: "scrollpage",
    component: ScrollPage
  },
  {
    path: "/TodoPage",
    name: "todopage",
    component: TodoPage
  },
  {
    path: "/TransitionTestPage",
    name: "transitiontestpage",
    component: TransitionTestPage
  }
];

const router = new VueRouter({
  // mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
